import React from "react";
import { FaChevronDown } from "react-icons/fa";
import ScrollIntoView from "react-scroll-into-view";
import logo2 from "../images/new.JPG";
import { BsGithub } from "react-icons/bs";
import { BsInstagram } from "react-icons/bs";
import { BsLinkedin } from "react-icons/bs";
import { AiOutlineMail } from "react-icons/ai";


function Home() {
  return (
    <>
      <div className="max-w-4xl mx-auto">
        <ScrollIntoView selector="#tech">
          <div className="mx-auto p-5 mt-2">
            <FaChevronDown className="animate-bounce mx-auto text-3xl text-blue-500 cursor-pointer" />
          </div>
        </ScrollIntoView>
        <div
          className="w-full h-screen grid place-items-center"
          data-aos="flip-right"
          data-aos-duration="800"
        >
          <div className="flex flex-col justify-center max-w-xs mx-auto bg-white shadow-xl rounded-xl p-10">
            <div className="">
              <img
                className="w-32 mx-auto shadow-xl rounded-full drop-shadow-sm"
                src={logo2}
              />
            </div>
            <div className="text-center mt-5">
              <p className="text-xl  sm:2xl text-gray-900">
                My name is Obinna.
              </p>
              <p className="text-xs  sm:text-base text-gray-600 pt-2 pb-4 px-5 w-auto inline-block border-b-2">
              This is a fashion editorial type stuff
              </p>
            </div>

            <div className="flex align-center justify-center mt-4">
              <a
                className="text-xl  m-1 p-1 text-gray-800 hover:bg-gray-800 rounded-full hover:text-white transition-colors duration-300"
                href="https://github.com/MuscleheadDeveloper"
                target="_blank"
              >
                <BsGithub />
              </a>
              <a
                className="text-xl m-1 p-1 text-gray-800 hover:bg-gray-800 rounded-full hover:text-white transition-colors duration-300"
                href="https://www.instagram.com/musclehead__developer/"
                target="_blank"
              >
                <BsInstagram />
              </a>
              <a
                className="text-xl m-1 p-1 text-blue-800 hover:bg-blue-800 rounded-full hover:text-white transition-colors duration-300"
                href="https://www.linkedin.com/in/obinna-njoku-859582178/"
                target="_blank"
              >
                <BsLinkedin />
              </a>
              <a
                className="text-xl m-1 p-1 text-gray-800 hover:bg-gray-800 rounded-full hover:text-white transition-colors duration-300"
                href="mailto:obimarionjoku@gmail.com"
                target="_blank"
              >
                <AiOutlineMail />
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Home;
