import logs from "../images/wahab1.JPG";
import logs2 from "../images/co.jpg";
import logs3 from "../images/img1.JPG";
import logs4 from "../images/img2.JPG";
import logs5 from "../images/img3.JPG";
import logs6 from "../images/imgg.JPG";
import logs8 from "../images/pro.JPG";
import logsb from "../images/looku.JPG";
import logsa from "../images/looku2.JPG";
import logA from "../images/IMG_2877.JPG";
import logB from "../images/wahab4.JPG";
import logC from "../images/wahab3.JPG";
import logD from "../images/wahab2.PNG";
import logE from "../images/sadboy2.JPG";
import logF from "../images/wahab5.JPG";
import logG from "../images/wahab6.JPG";
import logH from "../images/wahab7.JPG";
import logI from "../images/wahab8.JPG";
import logs9 from "../images/NERO.JPG";
import logs0 from "../images/weight.JPG";
import { BrowserRouter as Router } from "react-router-dom";

export default function GridGallery() {
  const images = [
    logA,
    logB,
    logC,
    logD,
    logE,
    logF,
    logG,
    logH,
    logI,
    logs2,
    logs3,
    logs4,
    logs,
    logs6,
    logs8,
    logs9,
    logs5,
    logs0,
    logsa,
    logsb,
  ];

  const jpegs = images.map((imageUrl) => (
    <div className="flex flex-wrap w-38 p-2 m-2 overflow-hidden justify-around bg-white shadow-2xl rounded-xl sm:w-52 ">
      <img src={imageUrl} className=" object-contain rounded" />
    </div>
  ));

  return (
    <div
      data-aos="flip-left"
      data-aos-duration="800"
      className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 pt-8 relative gridgallery"
    >
      {jpegs}
    </div>
  );
}
