import React from 'react'
import {FaArrowRight} from 'react-icons/fa'


function Contact() {
    return (
      <>
        <div>
            <div className="max-w-lg mx-auto w-full text-center pt-12 pb-10" data-aos="">
              <h1 className="text-2xl md:text-4xl">
                Interested in working together?
              </h1>
              <a href="mailto:obimarionjoku@gmail.com">
                <span className="mt-6 inline-block w-auto bg-black text-white text-base md:text-xl py-4 px-7 cursor-pointer">
                  <div className="flex flex-row items-center">
                    <span className="mr-3">Get in Touch</span>
                    <FaArrowRight color="white" />
                  </div>
                </span>
              </a>
            </div>
        </div>
      </>
    );
}

export default Contact
